<template>
	<div class="song">
		<div class="brief">
			<div class="bj">
				<div class="bjt">
					<img class="tp" :src="this.$route.params.b" alt="">
				</div>
				<div class="l">
					<img :src="this.$route.params.b" alt="">
					<div class="tu">
						<div class="box">
							<span class="playcount">{{Calculate(this.$route.params.c)}}</span>
							<div class="ej">
								<img class="erji" :src="ej" alt="">
							</div>
						</div>
					</div>
					<div class="gedan">
						<span>歌单</span>
					</div>
				</div>
				<div class="r">
					<div class="up">
						<span>{{this.$route.params.a}}</span>
					</div>
					<div class="below">
						<div class="bigt">
							<img :src="picture.avatarUrl" alt="">
						</div>
						<div class="character">
							<span class="tt">{{picture.nickname}}</span>
						</div>
					</div>
				</div>
			</div>
			<div class="synopsis">
				<div class="label">
					<span class="labels">
						标签 :
					</span>
					<div class="language" v-for="(item,index) in labelss" :key="index">
						{{item}}
					</div>
				</div>
				<div @click="dj()" class="abstract">
					<div class="box1">
						<span id="con" class="content">简介 : &nbsp;{{content}}</span>
					</div>
					<div class="jiantou">
						<img :src="sr" alt="">
					</div>
				</div>
			</div>
			<div class="songlist">
				<div class="songs">
					<span>歌曲列表</span>
				</div>
				<div class="lists">
					<div class="box">
						<li v-for="(item,index) in songlist" :key="index" @click="go({name:'player',params:{a:item.al.picUrl,b:item.id,son:songlist,index:index}})">
							<div class="indexs">
								{{index+1}}
							</div>
							<div class="serial" >
								{{item.name}}{{item.alia[0]?'('+item.alia[0]+')':''}}
							</div>
							<div class="geshou">
								{{item.ar[0].name}} - {{item.name}}
							</div>
							<div class="pofang">
								<div class="pf">
									<img src="../../public/wyyimg/bofang.png" alt="">
								</div>
							</div>
						</li>
					</div>
					
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name:'song',
		data(){
			return {
				a:'',
				b:'',
				ej:'/wyyimg/erji.png',
				picture:[],
				labelss:[],
				content:'',
				songlist:[],
				t:true,
				sr:'/wyyimg/xiatou.png'
			}
		},
		methods:{
			// 点击取消简介样式
			dj(){
				if(this.t){
					this.t=!this.t
					var a= document.querySelector('#con')
					a.setAttribute('class','')
					this.sr = '/wyyimg/shangtou.png'
				}else{
					this.t=!this.t
					var a= document.querySelector('#con')
					a.setAttribute('class','content')
					this.sr = '/wyyimg/xiatou.png'
				}
			},
			// 跳转路由
			go(path) {
				this.$router.push(path)
			},
			Calculate:function(params){
				if(params > 100000000){
					return (params / 100000000).toFixed(1) + '亿'
				}
				if(params > 10000){
					return (params / 10000).toFixed(1) + '万'
				}
			},
			
		},
		created(){
		
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/playlist/detail?id='+ this.$route.params.d)
			.then(res => {
				this.picture = res.data.playlist.creator
				console.log(this.picture)
			})
			.catch(err => {
				console.log("请求失败" + err)
			})
			
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/playlist/detail?id='+ this.$route.params.d )
			.then(res => {
				this.labelss = res.data.playlist.tags
				console.log(this.labelss)
			})
			.catch(err => {
				console.log("请求失败" + err)
			}),
			
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/playlist/detail?id='+ this.$route.params.d )
			.then(res => {
				this.content = res.data.playlist.description
				console.log(this.content)
			})
			.catch(err => {
				console.log("请求失败" + err)
			}),
			
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/playlist/detail?id='+ this.$route.params.d)
			.then(res => {
				this.songlist = res.data.playlist.tracks
				console.log(this.songlist)
			})
			.catch(err => {
				console.log("请求失败" + err)
			})
		}
		
		    
	}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	.song{
		width: 100%;
		.brief{
			width: 100%;
			overflow: hidden;
			.bj{
				width: 100%;
				height: 186px;
				position: relative;
				.bjt{
					width: 100%;
					height: 186px;
					position:absolute;
					left: 0;
					top: 0;
					overflow: hidden;
					.tp{
						width: calc(100% / 1);
						height: calc(100% / 1);
						filter: blur(28px);
						opacity: 1;
						transform: scale(1.5);
					}
				}
				.l{
					position: relative;
					width: 126px;
					height: 126px;
					top:30px;
					left: 15px;
					img{
						width: 100%;
					}
					.tu{
						position: absolute;
						width: 126px;
						left: 0;
						top: 0;
						.ej{
							width: 16px;
							float:right;
							.erji{
								width: 100%;
							}
						}
						.playcount{
							float:right;
							color: #FFFFFF;
							font-size: 12px;
							padding-left: 3px;
							margin-top: 2px;
							margin-right: 3px;
						}
					}
					.gedan{
						position: absolute;
						left: 0;
						top: 10px;
						width: 40px;
						height: 17px;
						border-radius:0px 9px 9px 0px ;
						background-color: #D24A41;
						span{
							display: block;
							color: #FFFFFF;
							font-size: 12px;
							text-align: center;
							line-height: 17px;
						}
					}
				}
				.r{
					width: 205px;
					height: 125px;
					position: absolute;
					right: 10px;
					top: 30px;
					.up{
						width: 205px;
						color: #FFFFFF;
						padding-top: 5px;
						font-size: 17px;
					}
					.below{
						width: 205px;
						height: 50px;
						margin-top: 30px;
						overflow: hidden;
						.bigt{
							width: 30px;
							height: 30px;
							border-radius:50%;
							float:left;
							margin-right:6px;
							img{
								width: 100%;
								height: 100%;
								border-radius:50%;
							}
						}
						.character{
							float: left;
							.tt{
								color: #E6E6E5;
								font-size:14px ;
							}
						}
					}
				}
			}
			.synopsis{
				width: 100%;
				padding-top: 10px;
				padding-left: 15px;
				overflow:hidden;
				background-color: #F8F8F8;
				.label{
					width: 100%;
					overflow:hidden;
					.labels{
						float: left;
						font-size: 14px;
						margin-right: 5px;
						color: #666666;
						margin-bottom: 10px;
					}
					.language{
						margin-left:5px ;
						margin-right: 5px;
						padding: 1px 8px;
						font-size: 12px;
						border:1px solid #EBEBEB;
						border-radius:10px 10px 10px 10px ;
						float: left;
						color: #666666;
					}
				}
				.abstract{
					width: 100%;
					padding-bottom: 18px;
					position: relative;
					.box1{
						padding: 0px 20px 0 0px;
						font-size: 14px;
						color: #666666;
					}
					.content{
						overflow: hidden;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 3;
						-webkit-box-orient: vertical;
					}
					.jiantou{
						position: absolute;
						right: 20px;
						bottom: 0;
						width: 20px;
						height: 20px;
						img{
							width: 100%;
						}
					}
				}
			}
			.songlist{
				width: 100%;
				background-color: #F8F8F8;
				.songs{
					width: 100%;
					height: 23px;
					font-size:12px;
					line-height: 23px;
					color: #666666;
					padding: 0px 10px 0px 10px;
					background-color: #EEEFF0;
				}
				.lists{
					width: 100%;
					list-style: none;
					.box{
						li{
							width: 100%;
							height: 54px;
							position: relative;
							border-bottom: 1px solid #EFEFEF;
							.indexs{
								float: left;
								width: 40px;
								height: 55px;
								text-align: center;
								line-height: 55px;
								color: #999;
							}
							.serial{
								white-space: nowrap;
								overflow: hidden;
								text-overflow: ellipsis;
								max-width: 283px;
								height: 43px;
								float: left;
								padding: 6px 0;
								font-size: 17px;
							}
							.geshou{
								position: absolute;
								left: 40px;
								bottom: 5px;
								color: #888888;
								font-size:12px ;
								white-space: nowrap;
								overflow: hidden;
								text-overflow: ellipsis;
								max-width: 283px;
							
							}
							.pofang{
								position: absolute;
								width: 35px;
								height: 35px;
								right: 0;
								top: 0;
								padding: 10px 10px 10px 10px;
								.pf{
									width: 25px;
									height: 25px;
									margin:5px 0px 0px 5px;
									img{
										width:100% ;
									}
								}
							}
						}
					}
				}
			}
		}
	}

</style>

